@import '~flatpickr/dist/flatpickr.min.css';

:host {
  display: block;
}

.answer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.speech-bubble-triangle {
  display: flex;
  max-width: 84%;
}

.triangle-topright {
  border-top: 7px solid var(--special-inactive);
  border-right: 7px solid transparent;
}

.speech-bubble-answer {
  background: var(--background-pastell-bg);
  border: 1px solid  var(--special-inactive);
  border-radius: 10px 0px 10px 10px;
  padding: 0.3125rem;
}

.speech-bubble-answer.date{
  display: flex;
}

.speech-bubble-answer.string, .speech-bubble-answer.string > form{
  display: flex;
  width: 100%;
}


.answer-button {
  margin: 0.3125rem;
  border: 1px solid var(--brand-main-brand);
  background: var(--background-white-bg);
  border-radius: 20px;
  color: var(--brand-main-brand);
  padding: 0.3125rem 0.625rem;
  font-weight: 600;
}

.answer-button.active,
.answer-input.answered {
  background: var(--brand-main-brand);
  color: var(--text-white-text);
}

.options-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.answer-input {
  margin: 0.0625rem !important;
  border: 1px solid var(--brand-main-brand);
  border-radius: 2px;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
  color:  var(--brand-main-brand);
  text-align: right;
  background-color: var(--background-white-bg);
}

textarea.answer-input{
  margin: 0.0625rem ;
  margin-right: 0.625rem !important;
  border: 1px solid var(--brand-main-brand);
  border-radius: 2px;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
  color:  var(--brand-main-brand);
  text-align: right;
  background-color: var(--background-white-bg);
  min-width: 13.9rem;
  min-height: 1.875rem;
  height: 1.875rem;
  width: 100%;
}

.answer-input:focus-visible {
  border-color: var(--brand-main-brand);
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.fade-in.left {
  transition: 0.5s all ease-in-out;
  transform: translate3d(-50px, 0, 0);
  opacity: 0;
}

.fade-in.right {
  transition: 0.5s all ease-in-out;
  transform: translate3d(50px, 0, 0);
  opacity: 0;
}

.fade-in.show {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.answer-input:disabled, .answer-button:disabled{
  opacity: .4;
}
